import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { IonicSelectableComponent } from 'ionic-selectable';
class Port {
  public id: number;
  public name: string;
}

@Component({
  selector: 'search-by-name',
  templateUrl: './search-by-name.component.html',
  styleUrls: ['./search-by-name.component.scss'],
})
export class SearchByNameComponent implements OnInit {

  ports: Port[];
  port: Port;

  @Input() filtrationList : any ;
  @Input() filterPageName : number;
  @Input() SearchByNameHidden : boolean = true; 
  @Output() onSearch : EventEmitter<any> = new EventEmitter<any>();

  constructor() { }


  ngOnInit() {
    console.log(this.filtrationList);
    console.log(this.filterPageName)
  }

  portChange(event: {
    component: IonicSelectableComponent,
    value: any
  }) {
    console.log('port:', event.value);
    this.onSearch.emit(event.value);
  }

}
