import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'img-upload',
  templateUrl: './img-upload.component.html',
  styleUrls: ['./img-upload.component.scss'],
})
export class ImgUploadComponent implements OnInit {



  isThereImg   : boolean = false;
  imgSrc       : string;
  selectedFile : any;

  @Input() isPaymentTransfer : boolean = false ;
  uploadUrl = BaseUrl.baseUrl + '/api/FileUpload/UploadFileAsync';

  @Output() onUploadIdImageUrl : EventEmitter<any> = new EventEmitter<any>();

  constructor(private http: HttpClient,) { }

  ngOnInit() { }

  imgSelected(event) {

    this.selectedFile = event.target.files[0];

    const uploadData = new FormData();
    uploadData.append('myFile', this.selectedFile, this.selectedFile.name);
    
    new Promise((resolve,reject)=>{
      
    this.http.post(this.uploadUrl, uploadData).subscribe((data: any) => {
      this.imgSrc = data.result.virtualPath;
      resolve(data.result.virtualPath)
    }, err =>{
      console.log(err)
      reject(err)
    });
    }).then((imgSrc)=>{
      console.log(imgSrc);
      this.isThereImg = true;
      this.onUploadIdImageUrl.emit(imgSrc)
    }).catch((err)=>{
      console.log(err);
      this.isThereImg = false;
    })

  }

}
