import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Platform, ModalController, ToastController } from '@ionic/angular';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Observable } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { LoadingService } from 'src/app/services/loading/loading.service';
import { google } from 'google-maps'


declare var google :google;

@Component({
  selector: 'map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit {

  public map;
  private lat;
  private lng;

  @Input() userId: string;

  registeredCustomer_msg: string;

  @Output() onSetLocation : EventEmitter<any> = new EventEmitter<any>();


  constructor(
    private platform: Platform,
    private geolocation: Geolocation,
    private _loadingController: LoadingService,
    private modalController: ModalController,
    private translate: TranslateService,
    private toastController: ToastController,
  ) { }

  ngOnInit() {


    this.registeredCustomer_msg =(this.translate.currentLang === 'ar')?
     "لقد تم حفظ موقعك الحالي بنجاح " : "Your current location has been successfully saved";


    console.log(this.userId)
    this.LocationsLoading();
    this.platform.ready()
      .then(() => {
        this.map = this.createMap();
      })

  }

  createMap(location = new google.maps.LatLng(this.lat, this.lng)) {
    let mapOptions = {
      center: location,
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      disableDefaultUI: false,
      panControl: true,
      zoomControl: true,
      zoomControlOptions: {
        position: google.maps.ControlPosition.LEFT_BOTTOM
    },
      mapTypeControl: true,
      scaleControl: false,
      streetViewControl: true,
      overviewMapControl: true,
      rotateControl: true
    }
    let mapEl = document.getElementById('map');
    let map = new google.maps.Map(mapEl, mapOptions);

    return map;
  }


  getCurrentLocation() {
    //this.getCu();

    this._loadingController.loading_msg = "Locating...";

    this._loadingController.present();
    let options = { timeout: 10000, enableHighAccuracy: true }

    let locationObs = Observable.create(observable => {

      this.geolocation.getCurrentPosition(options)
        .then(resp => {
          this.lat = resp.coords.latitude;
          this.lng = resp.coords.longitude;

          let location = new google.maps.LatLng(this.lat, this.lng)
          observable.next(location);

          this._loadingController.dismiss();
        }, (err) => {
          this._loadingController.dismiss();
          console.log('Geolocation err: ' + err)
        });

    })


    return locationObs;

  }


  centerLocation(location) {
    if (location) {
      this.map.panTo(location)
    } else {
      this.getCurrentLocation().subscribe(currentLocation => {
        this.map.panTo(currentLocation)
      })
    }
  }


  LocationsLoading() {


    let Address_latitude = window.localStorage.getItem("Address_latitude");
    let Address_longitude = window.localStorage.getItem("Address_longitude");

    console.log(Address_latitude)

    if (Address_latitude && Address_longitude) {
      this.lat = + Address_latitude;
      this.lng = + Address_longitude;
      console.log(this.lat)
    } else {
      this.getCurrentLocation().subscribe(location => {
        this.centerLocation(location)
      }, err => { console.log(err) })
    }
  }



  mapGetCenter() {
    this.lat = this.map.getCenter().lat();
    this.lng = this.map.getCenter().lng();
    console.log("lat : ", this.lat)
    console.log("lng : ", this.lng)
     window.localStorage.setItem("Address_latitude", this.lat);
     window.localStorage.setItem("Address_longitude" , this.lng);

     let locationValue = {
       lat : this.lat,
       lng : this.lng
     };
     
     this.onSetLocation.emit(locationValue);
     
    //this.LocationSaveing()
    this.showToast_msg(this.registeredCustomer_msg);
  }

  // async LocationSaveing() {
  //   const modal = await this.modalController.create({
  //     component: AddressSavingComponent,
  //     cssClass: 'my-custom-class',
  //     componentProps: {
  //       'Address_latitude': this.lat,
  //       'Address_longitude': this.lng,
  //       'userId': this.userId
  //     }
  //   });
  //   return await modal.present();
  // }


  async showToast_msg(msg: string) {
    let toast = await this.toastController.create({
      message: this.registeredCustomer_msg ,
      duration: 3000,
      cssClass: "toast-center toast-warning",
      position: "top"
    });

    await toast.present();
  }

}
