import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { SettingsService, EvaluateModle } from 'src/app/services/Settings/Settings';
import { LoadingService } from 'src/app/services/loading/loading.service';


@Component({
  selector: 'app-evaluations',
  templateUrl: './evaluations.component.html',
  styleUrls: ['./evaluations.component.scss'],
})
export class EvaluationsComponent implements OnInit {

  @ViewChild('rating',{static:false}) rating : any;

  @Input() request_Id: any ;
  ratingNumber : number ;
  evaluateModel ;
  evaluationComment : string ;

  constructor(
    private modalController  : ModalController,
    private _setting         : SettingsService,
    private _loading         : LoadingService
  ) { }

  ngOnInit() { 
    this.evaluateModel = new EvaluateModle() ;
    console.log(this.request_Id)
    this.evaluateModel.requestId = this.request_Id;
  }

  close(){
    this.modalController.dismiss();
  }

  logRatingChange(rating){
    console.log("changed rating: ",rating);
    this.ratingNumber  = rating;
    this.evaluateModel.evaluation = this.ratingNumber ;
    this.evaluateModel.evaluationComment =this.evaluationComment;
    // do your stuff
}

evaluateRequest(){
  this._loading.present();
 setTimeout(() => {
  this._setting.evaluateRequest(this.evaluateModel).subscribe(data =>{
    console.log(data);
    this._loading.dismiss();
  })
  this.close();
 }, 1500);
}


}
